import React, {useEffect, useState} from 'react';

import './styles/App.css';
import Header from './components/Header';
import Content from "./components/Content";
import initialData from './initialData/photos.json';
import {ApiResponseType} from "./types/photos.types";
import CookieConsent from 'react-cookie-consent';
import ViewersWarning from "./components/ViewersWarning";
import Loader from "./components/Loader";
import Error from "./components/Error";

const GetRecentApi = (page: number = 1) => `https://api.flickr.com/services/rest/?method=flickr.photos.getRecent&api_key=84bf9b29bce8db001d1e58dbec8a5770&per_page=25&page=${page}&format=json&nojsoncallback=1`;

function App() {
    const initialSearchTerms: string[] = [];
    const [page, setPage] = useState(1);
    const [photos, setPhotos] = useState(initialData.photos.photo);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(false);
    const [searchTerms, setSearchTerms] = useState(initialSearchTerms);
    const [scrollTop, setScrollTop] = useState(0);
    const [seenApis, setSeenApis] = useState('');
    const [inSearch, setInSearch] = useState(false);

    function loadPhotosFromNetwork() {
        if (!loading) {
            const api = GetRecentApi(page);
            if (seenApis.includes(api)) return;
            setSeenApis(seenApis + ';' + api);
            setLoading(true);
            fetch(api)
                .then((res: Response) => res.json())
                .then((data: ApiResponseType) => {
                    const _photos = page === 1 ? [...data.photos.photo] : [...photos, ...data.photos.photo];
                    setPhotos(_photos);
                    // console.log('fetched from get recent api', api);
                    setLoading(false);
                })
                .catch((e: Error) => {
                    // console.error(e);
                    setLoading(false);
                    setError(true);
                });
        }
    }

    const scrollListener = () => {
        const {scrollTop} = document.documentElement;
        setScrollTop(scrollTop);
        if (scrollTop >= document.documentElement.scrollHeight - 1 * document.documentElement.offsetHeight) {
            setPage(page + 1);
            document.removeEventListener('scroll', scrollListener);
        }
    };

    useEffect(() => {
        if (!inSearch) {
            loadPhotosFromNetwork();
            document.addEventListener('scroll', scrollListener, true);
            return () => {
                document.removeEventListener('scroll', scrollListener);
            };
        }
    }, [scrollTop]);

    function handleSearchResponse(data: ApiResponseType, term: string) {

        setInSearch(true);
        setPhotos(data.photos.photo);
        if (term !== '' && !searchTerms.includes(term)) {
            setSearchTerms([...searchTerms, term]);
            localStorage.setItem('search-terms', JSON.stringify([...searchTerms, term]));
        }
        
    }

    function exitFromSearch() {
        setPage(1);
        setPhotos(initialData.photos.photo);
        setInSearch(false);
        loadPhotosFromNetwork();
    }

    return (
        <div className="App" style={{opacity: loading ? 0.7 : 1}}>
            <ViewersWarning/>
            <CookieConsent
                location="bottom"
                buttonText="I understand. Use cookies."
                cookieName="slickr-cookie-consent"
                style={{background: "#2B373B", fontWeight: "bold"}}
                buttonStyle={{color: "#4e503b"}}
            >
                This website uses cookies to enhance the user experience.
                <br/>
                Experience will be suboptimal without it. Please do not use app if you do not consent.
            </CookieConsent>

            <Header onSearchSuccess={handleSearchResponse} onSearchClear={exitFromSearch}/>
            <Content photos={photos}/>
            <Error show={error}/>
            <Loader show={loading}/>
        </div>
    );
}

export default App;
