import React from 'react';
import {Container, Navbar} from "react-bootstrap";
import Search from "./Search";

function Header(props: any) {
    return (
        <Navbar bg="light" expand="lg" fixed={"top"}>
            <Container>
                <Navbar.Brand href="/slickr" onClick={props.onSearchClear}>Slickr</Navbar.Brand>
                <Search onSearchSuccess={props.onSearchSuccess} onSearchClear={props.onSearchClear}/>
            </Container>
        </Navbar>
    );
}

export default Header;
