import {PhotoType, ImageType} from "../types/photos.types";

const PHOTO_SIZE = {
    thumbnail75: '_s',
    thumbnail150: '_q',
    thumbnail100: '_t',
    small240: '_m',
    small320: '_n',
    small400: '_w',
    medium500: '',
    medium640: '_z',
    medium800: '_c'
};

function photosToUrls(photos: PhotoType[]): string[] {
    return photos.map(makeUrl);
}

function makeUrl(photo: PhotoType): string {
    return `http://farm${photo.farm}.static.flickr.com/${photo.server}/${photo.id}_${photo.secret}${PHOTO_SIZE.small320}.jpg`;
}

function photosToImages(photos: PhotoType[]): ImageType[] {
    return photos.map((p: PhotoType) => {
        return {url: makeUrl(p), title: p.title};
    });
}

function _nTo_z(url: string): string {
    return url.replace('_n', '_z');
}

export {makeUrl, photosToUrls, photosToImages, _nTo_z};
