import React, {useState} from "react";
import Styled from 'styled-components';
import ImagePreview from "./ImagePreview";

const ImageContainer = Styled.div`
    border: 1px solid #ddd;
    margin: 1rem auto;
    padding: 1rem;
    box-shadow: 1px 1px 3px #eee;
    width: calc(100% - 6rem);
    height: 300px;
    vertical-align: top;
    
    @media (min-width: 960px) {
        display: inline-block;
        width: 300px;
        margin: .5rem;
        padding: 1rem;
        a.link {
            display: none;
        }
    }
    
    & img {
        width: 100%;
        max-height: calc(300px - 4rem);
    }
    & label {
        text-align: center;
        width: 100%;
        background-color: rgba(132, 132, 132, 0.2);
        color: #333;
        font-weight: bold;
        padding: .5em;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
    & a.link {
        text-decoration: none;
        color: red;
        padding: 0 .2em;
    }
`;

type ImageProps = {
    src: string;
    alt: string;
    height?: string;
    width?: string;
    title?: string;
    url: string;
}

function Image(props: ImageProps) {
    const [showPreview, setShowPreview] = useState(false);

    function hidePreview() {
        setShowPreview(false);
    }

    return <ImageContainer className={'image-container'} onClick={() => setShowPreview(true)}>
        {props.title && <label>{props.title}</label>}
        <img {...props} />
        {props.url && <a className="link" href={props.url}>download</a>}
        {showPreview && <ImagePreview shouldShow={showPreview} onHide={hidePreview} {...props}/>}
    </ImageContainer>;
}

export default Image;

