import React, {useRef, useState} from "react";

import {Button, Form, FormControl} from "react-bootstrap";
import debounce from 'lodash.debounce';

const searchApi = (text: string) => `https://api.flickr.com/services/rest/?method=flickr.photos.search&api_key=84bf9b29bce8db001d1e58dbec8a5770&text=${text}&format=json&nojsoncallback=1`;

function Search(props: any) {
    const [searchTerm, setSearchTerm] = useState('');
    const [error, setError] = useState(true);

    const debouncedSearch = useRef(debounce(url => onSearch(searchApi(url)), 1000)).current;

    function onSearch(url: string) {
        // console.log('search', url);
        fetch(url)
            .then((res: Response) => res.json())
            .then(data => {
                console.log(`data from search on: `, searchTerm, data);
                props.onSearchSuccess(data, searchTerm);
            })
            .catch((e: Error) => {
                console.error('error while searching...', e);
                setError(true);
            });
    }

    function handleClear() {
        setSearchTerm('');
        setError(false);
        props.onSearchClear();
    }

    return (
        <>
            {!error &&
            <Form className="d-flex">
                <FormControl
                    type="search"
                    placeholder="Search"
                    className="input search-input"
                    aria-label="Search"
                    onChange={e => {
                        setSearchTerm(e.target.value);
                        debouncedSearch(e.target.value);
                    }}
                    value={searchTerm}
                />
                {searchTerm &&
                <Button variant="outline-success" onClick={() => onSearch(searchApi(searchTerm))}>Search</Button>}
                {searchTerm &&
                <Button variant="warning" className="clear-current-search" onClick={handleClear}>X</Button>}
            </Form>
            }
            {
                error && <>
                    Search failed
                    <Button variant="warning" className="clear-current-search" onClick={handleClear}>X</Button>
                </>
            }
        </>
    );

}

export default Search;
