import React from 'react';
import Styled from "styled-components";
import Image from "./Image";
import {ImageType} from '../types/photos.types';
import {photosToImages} from "../utilities/get-recent.utils";

const ContentContainer = Styled.section`
    width: 100%;
    padding: 1em;
    
    @media (min-width: 960px) {
        width: 95%;
        margin: auto;
    }
`;

function Content(props: any) {

    return <ContentContainer>
        {
            photosToImages(props.photos).map((image: ImageType, index: number) => {
                return <Image
                    key={`photo-${index}`}
                    src={image.url}
                    alt={`${image.title} at ${image.url}`}
                    title={image.title}
                    url={image.url}
                />;
            })
        }

    </ContentContainer>;
}

export default Content;
