import React, {useState} from "react";
import {Button, Modal} from "react-bootstrap";

function ViewersWarning() {
    const shouldShowAgain = localStorage.getItem('content-warning');
    const [show, setShow] = useState(shouldShowAgain !== 'ok');

    const handleClose = () => {
        setShow(false);
        window.close();
    };

    const handleOk = () => {
        setShow(false);
        localStorage.setItem('content-warning', 'ok');
    };

    return (
        <>
            <Modal
                show={show}
                onHide={handleClose}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header>
                    <Modal.Title>Warning: Unfiltered content.</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    This application fetches images from public service.<br/>
                    People may choose to upload disturbing content, containing but not limited to:<br/>
                    sexual, child abuse, self-harm, gore, mutilation etc.
                    <br/>
                    Continue only if you are okay with this.
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Close the browser tab/window.
                    </Button>
                    <Button variant="primary" onClick={handleOk}>I understand. Let me continue to app anyway.</Button>
                </Modal.Footer>
            </Modal>
        </>
    );
}

export default ViewersWarning;
